[data-tooltip] {
  position: relative;
  z-index: 2;
  cursor: pointer;
}

/* Hide the tooltip content by default */
[data-tooltip]:before,
[data-tooltip]:after {
  visibility: hidden;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  pointer-events: none;
  white-space: pre;
  word-wrap: break-word;
  z-index:99;

}

/* Position tooltip above the element */
[data-tooltip]:before {

    content: attr(data-tooltip);
    position: absolute;
    bottom: 105%;
    left: 50%;
    margin-bottom: 5px;
    margin-left: -65px;
    width: 146px;
    border-radius: 3px;
    background-color: hsl(0deg 0% 100% / 104%);
    text-align: center;
    font-size: 15px;
    padding: 15px 20px;
    line-height: 1.5;
    color: #1a1a1a;
    box-shadow: 0px 4px 16px rgb(58 78 88 / 15%);
    text-align: left;
    border-radius: 10px;
}

/* Triangle hack to make tooltip look like a speech bubble */
[data-tooltip]:after {
  position: absolute;
  bottom: 105%;
  left: 50%;
  margin-left: 17px;
  width: 0;
  border-top: 5px solid hsl(0deg 0% 100% / 90%);
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  content: " ";
  font-size: 0;
  line-height: 0;
}

/* Show tooltip content on hover */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
  visibility: visible;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

